.customized-card {
  display: flex;
  border-radius: 10px;
  margin-bottom: 5%;
  height: 140px;
  overflow: hidden;
  min-width: 0;
}

.customized-card-image {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.customized-card-image img {
  width: 110px;
  height: 110px;
  flex-shrink: 0;
  border-radius: 10px;
  margin: 14px 16px;
  object-fit: cover;
}

.customized-card.user {
  background: var(--secondary-color-sky-blue-translucent);
}

.customized-card.lab {
  background: var(--secondary-color-dark-green-translucent);
}

.customized-card.user-profile {
  background: rgba(246, 223, 164, 0.36);
}

.customized-card-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-grow: 1;
  padding: 14px 16px;
}

.customized-card-content h2 {
  padding-right: 18px;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: #000;
  margin: 0;
}

.customized-card-content p {
  padding-right: 18px;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 400;
  color: #000;
  margin: 4px 0;
}

.customized-card-footer > div {
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 400;
  color: #55565a;
}

.customized-card-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  position: absolute;
  bottom: 16px;
  right: 24px;
}

.customized-card-footer > div,
.card-footer button {
  flex-shrink: 0;
}

.customized-card-footer button {
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
}

.img {
  width: 100%;
  height: 100vh;
  margin: auto;
}

.ant-carousel {
  width: 100%;
}

.story-image-gallery {
  width: 100%;
}
