.signup-other-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.signup-form-section {
  width: 100%;
  display: flex;
  justify-content: center;
}

.signup-form-section :global(.ant-checkbox-wrapper) {
  margin-top: 8px;
}

.signup-form-section :global(.ant-checkbox) {
  align-self: flex-start;
  padding-top: 4px;
  min-width: 16px;
}

.signup-form-section :global(.ant-checkbox-inner::after) {
  top: 56%;
}

.signup-other-section h4 {
  font-weight: 400;
}

.social-signup-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 16px 0;
  gap: 24px;
}

.social-signup-icons img {
  height: auto;
}

.error {
  text-align: center;
  color: red;
  margin: 8px;
  font-family: var(--font-family-body);
  font-size: var(--font-size-h5);
}