.profile-banner {
  width: 100%;
  height: 96px;
  align-items: center;
  object-fit: cover;
}

.profile-img {
  border-radius: 50%;
  width: 108px;
  height: 108px;
  position: absolute;
  z-index: 1;
  margin-top: -55px;
}

.profile-top {
  display: flex;
  justify-content: center;
  padding: 5px 5%;
}

.profile-name {
  text-align: center;
  padding-top: 25px;
}

.user-name {
  text-align: center;
  padding-top: 5px;
}

.user-bio,
.topic-container {
  text-align: center;
  padding: 5px 5%;
}

.topic-title {
  color: gray;
  text-decoration: underline;
}

.user-topic {
  color: gray;
  padding-top: 5px;
}

.log-out {
  color: red;
  cursor: pointer;
  padding: 10px;
  text-decoration: none;
  font-family: var(--font-family-body);
  font-size: var(--font-size-h4);
}

.profile-edit {
  display: flex;
  align-items: flex-end;
  gap: 8px;
  justify-content: center;
  margin-top: 8%;
}
