@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Homemade+Apple&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Jaldi:wght@400;700&display=swap");

:root {
  /* Colors */
  --primary-color-navy-blue: rgba(15, 83, 124, 1);

  --primary-color-sky-blue: rgba(7, 119, 188, 1);
  --primary-color-sky-blue-translucent: rgba(7, 119, 188, 0.6);

  --primary-color-light-blue: rgba(150, 198, 216, 1);

  --primary-color-green: rgba(192, 213, 204, 1);

  --primary-color-yellow: rgba(246, 223, 164, 1);

  --secondary-color-light-blue: rgba(202, 232, 250, 1);

  --secondary-color-sky-blue: rgba(107, 190, 240, 1);
  --secondary-color-sky-blue-translucent: rgba(107, 190, 240, 0.36);
  --secondary-color-sky-blue-dark: rgba(16, 83, 125, 1);

  --secondary-color-light-green: rgba(146, 187, 95, 1);

  --secondary-color-dark-green: rgba(78, 138, 112, 1);
  --secondary-color-dark-green-translucent: rgba(78, 138, 112, 0.36);

  --link-color-blue: rgba(46, 124, 246, 1);

  --primary-warning-color-red: rgba(251, 23, 23, 1);

  /* Font families */
  --font-family-heading: "Open Sans", sans-serif;
  --font-family-body: "Montserrat", sans-serif;

  /* Font sizes */
  --font-size-h1: 24px;
  --font-size-h2: 20px;
  --font-size-h3: 18px;
  --font-size-h4: 16px;
  --font-size-h5: 14px;
  --font-size-h6: 12px;
  --font-size-body-large: 18px;
  --font-size-body-small: 16px;
}
